.form__container {
    margin: 0 auto;
    max-width: 760px;

}
.form__title {
    font-size: 48px;
    line-height: 1.23;
    font-weight: 600;
    text-align: center;  
}
.form__desc {
    font-size: 24px;
    line-height: 1.5;
    font-weight: 300;
    text-align: center;
}

form {
    display: flex;
    justify-content: space-between;
    width: 100%;
    flex-direction: column;
    margin-top: 60px;
    @media (min-width: 992px) {
        flex-direction: row;        
    }
    input {
        color: #000000;
        border: 1px solid #b3b3b3;
        background-color: #ffffff;
        border-radius: 100px;
        padding: 15px 20px;
        font-size: 16px;
        line-height: 1.33;
        max-width: 100%;
        width: 100%;
        margin-bottom: 20px;
        @media (min-width: 992px) {
            max-width: 280px;
            margin-bottom: 0;
        }
    }
    button {
        color: #ffffff;
        background-color: #e61e53;
        font-size: 16px;
        border-radius: 30px;
        background-repeat: 30px;
        padding: 15px;
        display: flex;
        justify-content: center;
        border: none;
        font-weight: 700;
        cursor: pointer;
        max-width: 100%;
        width: 100%;
        transition: .4s;
        @media (min-width: 992px) {
            max-width: 161px;
        }
        &:hover {
            background-color: rgb(184, 201, 34);
        }
    }
}
